.cards {
    padding: 4rem;
    background: #fff;
}
  
h1 {
    text-align: center;
    font-size: 75px;
}
.cards-box{
    background-color: rgba(145, 69, 25, 0.55);
    z-index: 1;
    border-radius: 20px;
    padding-top: 10px;
    justify-content: center;
}
.cards-container {
    display: flex;
    flex-flow: column;
    align-items: center;
    max-width: 1120px;
    width: 90%;
    margin: 0 auto;
}
  
.cards-wrapper {
    position: relative;
    margin: 50px 0 45px;
}
  
.cards-items {
    margin-bottom: 24px;
    padding-left: 0ch;
}
  
.cards-item {
    display:flex;
    flex: 1 1;
    margin: 0 1rem;
    border-radius: 10px;
}
  
.cards-item-link {
    display: flex;
    flex-flow: column;
    width: 100%;
    box-shadow: 0 6px 20px rgba(56, 125, 255, 0.17);
    -webkit-filter: drop-shadow(0 6px 20px rgba(56, 125, 255, 0.017));
    filter: drop-shadow(0 6px 20px rgba(56, 125, 255, 0.017));
    border-radius: 10px;
    overflow: hidden;
    text-decoration: none;
    background-color: #fff;
    margin-left: 0rem;
}
  
.cards-item-pic-wrap {
    position: relative;
    width: 100%;
    height: 100%;
    padding-top: 67%;
    overflow: hidden;
    margin-left: 0;
    margin-top: 0cm;

}
  
.fade-img {
    -webkit-animation-name: fade-img;
            animation-name: fade-img;
    -webkit-animation-duration: 2s;
            animation-duration: 2s;
}
  
.cards-item-pic-wrap::after {
    content: attr(data-category);
    position: absolute;
    left:0cm;
    bottom: 0;
    margin-left: 10px;
    padding: 6px 8px;
    max-width: calc((100%) - 60px);
    font-size: 12px;
    font-weight: 700;
    color: #fff;
    background-color: #1f98f4;
    box-sizing: border-box;
}
  
.cards-item-img {
    position: absolute;
    top: 0;
    right: 0;
    display: block;
    width: 100%;
    max-width: 100%;
    height: 100%;
    max-height: 100%;
    object-fit: cover;
    transition: all 0.2s linear;
    border: 0cm;
    margin: 0cm;
    padding: 0cm;
    margin-left: -2cm;
}
  
.cards-item-img:hover {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
}
  
.cards-item-info {
    padding: 20px 30px 30px;
}
  
.cards-item-text {
    color: #252e48;
    font-size: 18px;
    line-height: 24px;
}
  
@media only screen and (min-width: 1200px) {
    .content-blog-container {
